<template>
  <div id="summary">
      <!-- 标题 -->
      <el-col
      style="
        line-height: 40px;
        border: 1px;
        border-style: solid solid none solid;
      "
      >进仓单</el-col
    >
      <!-- 表格区 -->
      <el-table  border id="middle" :data="formData.detail">
        <el-table-column
          type="index"
          label="序号"
          fixed
          width="60px"
        ></el-table-column>
        <el-table-column
          v-for="item in newEnterStoreTableList"
          :prop="item.prop"
          :label="item.label"
          :width="item.width"
          :key="item.index"
        >
        </el-table-column>
        <el-table-column
          label="进仓编号"
        >
        <template slot-scope="scope">
              <el-input v-model="scope.row.warehouseEntryNo"></el-input>
            </template>
        </el-table-column>
      </el-table>
      <el-row>
      <el-col class="text-left"
          ><div
            
          >
            到货日期
          </div>
          <div style="display: inline-block;min-width:90%">
            <el-input  v-model="formData.warehouseEntryTime"></el-input></div
        ></el-col> 
      <div class="multi-input"><el-col :span="6" v-for="item in footeritems" :key="item.index">
          <el-col style="border-left: 1px solid;border-right: 1px solid;border-bottom:1px solid;height:100%" :span="8">{{item.label}}</el-col>
          <el-col style="border-bottom:1px solid" :span="16">{{formData[item.value]}}</el-col>
      </el-col></div>
      <el-col style="height:120px" class="text-left"
          ><div
            style="line-height:120px;max-width:10%"
          >
            注意事项
          </div>
          <div style="display: inline-block;min-width:90%;">
            <el-input type="textarea"  v-model="formData.warehouseEntryNote"></el-input></div
        ></el-col>
        </el-row>
      <el-row class="text-right">
        <el-button type="primary" @click="submit">提交</el-button>
        <el-button type="warning" @click="cancel">取消</el-button>
      </el-row>
  </div>
</template>

<script>
import {GetSingleWarehouseEntry,UpdateWarehouseEntry} from 'network/enterStore.js'

export default {
    data(){
        return{
            mark:'',
            formData:{tableData:[]},
            newEnterStoreTableList:[
                {label:'订单号',prop:'orderNo',width:''},
                {label:'货号',prop:'itemNo',width:''},
                {label:'数量(双)',prop:'shipmentNumber',width:''},
                {label:'箱数',prop:'boxNumber',width:''},
                {label:'体积',prop:'volume',width:''},
            ],
            footeritems:[{label:"箱数",value:"totalBoxNumber"},{label:"毛重",value:"totalGrossWeight"},{label:"净重",value:"totalNetWeight"},{label:"体积",value:"totalVolume"}]
        }
    },
    created(){
      this.inital();
    },
    methods:{
      //初始化获得数据
      inital(){
        var data = this.$route.query.iid;
        GetSingleWarehouseEntry(data).then(res=>{
          this.formData = res.result;
        })
      },
      //提交
      submit(){
        let data = this.formData;
        UpdateWarehouseEntry(data).then(res=>{
          if(res.success){
            this.$message.success("提交成功");
          }
        })
      },
      //取消
      cancel() {
      this.$router.go(-1);
      },
    }
}
</script>

<style scoped>
.text-left {
  border: solid;
  border-width: 0 1px 1px 1px;
  height: 56px;
}
.text-left>div:first-child {
  display: inline-block;
  width: 60px;
  height: 100%;
  border-right: 1px solid;
  line-height: 56px;
}
.el-row {
  margin-bottom: 10px;
  line-height: 40px;
}
.multi-input>.el-col:last-child{
  border-right: 1px solid;
}
</style>