import {request} from './request'

//获得进舱单列表
export async function GetAllWarehouseEntry(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/GetAllWarehouseEntry',
        data:data
    })
}
//批量更新状态
export async function UpdateWarehouseEntryState(data){
    return await request({
        method:'PUT',
        url:'/api/BusinessUnit/UpdateWarehouseEntryState',
        data:data
    })
}
//获得单个进仓单信息
export async function GetSingleWarehouseEntry(data){
    return await request({
        url:'/api/BusinessUnit/GetSingleWarehouseEntry',
        params:{input:data}
    })
}
//更新进仓单
export async function UpdateWarehouseEntry(data){
    return await request({
        method:'PUT',
        url:'/api/BusinessUnit/UpdateWarehouseEntry',
        data:data
    })
}